







































































import { Component, Mixins, Prop } from 'vue-property-decorator'
import includes from 'lodash/includes'
import PopupMixin from '@/mixins/popup'
import { namespace } from 'vuex-class'

const beBase = namespace('beBase')

@Component
export default class PopupFilterAddresses extends Mixins(PopupMixin) {
  @Prop({
    required: true,
    type: Array,
    default: () => {
      return []
    }
  })
  listAssetNetwork!: Array<Record<string, any>>
  @beBase.State('coinMain') coinMain!: string

  filter: Record<string, any> = {
    currency: null,
    fromCreatedAt: null,
    toCreatedAt: null,
    network: null
  }
  listWallet: Array<Record<string, any>> = [
    {
      id: 0,
      name: 'Bitcoin',
      symbol: 'btc',
      icon: 'icon-btc'
    },
    {
      id: 1,
      name: 'Ethereum',
      symbol: 'eth',
      icon: 'icon-eth'
    },
    {
      id: 2,
      name: 'Tether',
      symbol: 'usdt',
      icon: 'icon-usdt',
      disabled: true
    },
    {
      id: 3,
      name: 'USDC',
      symbol: 'usdc',
      icon: 'icon-usdc',
      disabled: true
    },
    {
      id: 4,
      name: 'BNB',
      symbol: 'bnb',
      icon: 'icon-bnb',
      disabled: true
    }
  ]

  listStatus: Array<Record<string, any>> = [
    {
      id: 0,
      label: 'Ethereum ',
      value: 'ERC20'
    },
    {
      id: 1,
      label: 'Binance Smart Chain',
      value: 'BEP20'
    },
    {
      id: 2,
      label: 'Testnet ',
      value: 'TESTNET'
    }
  ]
  errorType = ''
  renderIconAsset(currency: string): string {
    switch (currency) {
      case 'BNB':
        return 'icon-bnb'
      case 'BTC':
        return 'icon-btc'
      case 'ETH':
        return 'icon-eth'
      case 'CLM':
        return 'icon-clm'
      case 'USDC':
        return 'icon-usdc'
      case 'USDT':
        return 'icon-usdt'

      default:
        return 'icon-lin'
    }
  }
  get getListWallet(): Array<Record<string, any>> {
    if (this.coinMain === 'LYNK') {
      return [
        {
          id: 5,
          name: 'Lynkey',
          symbol: 'lynk',
          icon: 'icon-lin'
        },
        ...this.listWallet
      ]
    }
    return [
      {
        id: 5,
        name: 'CLM',
        symbol: 'clm',
        icon: 'icon-clm'
      },
      ...this.listWallet
    ]
  }

  public handleReset(): void {
    this.filter = {
      currency: null,
      fromCreatedAt: null,
      toCreatedAt: null,
      network: null
    }
    // this.setOpenPopup({
    //   popupName: 'popup-filter-transaction',
    //   isOpen: false
    // })
    // this.$emit('filter', this.filter)
  }

  checkValid(): boolean {
    let fromDate = new Date(this.filter.fromCreateAt)
    let toDate = new Date(this.filter.toCreateAt)
    if (fromDate.getTime() > toDate.getTime()) {
      this.errorType = 'time'
      return false
    }
    return true
  }
  get pickerOption(): any {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const _this = this
    return {
      disabledDate(time: Date) {
        return _this.disableTime(time, 'from-to')
      }
    }
  }
  get pickerOption2(): any {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const _this = this
    return {
      disabledDate(time: Date) {
        return _this.disableTime(time, 'to-from')
      }
    }
  }

  disableTime(time: Date, type: string): any {
    if (type === 'from-to') {
      if (this.filter.fromCreatedAt) {
        return time.getTime() < new Date(this.filter.fromCreatedAt).getTime()
      }
    } else {
      if (this.filter.toCreatedAt) {
        return time.getTime() > new Date(this.filter.toCreatedAt).getTime()
      }
    }
  }

  handleApply(): void {
    this.setOpenPopup({
      popupName: 'popup-filter-addresses',
      isOpen: false
    })

    if (this.filter.currency === '') {
      this.filter.currency = null
    }
    if (this.filter.fromCreatedAt === '') {
      this.filter.fromCreatedAt = null
    }
    if (this.filter.toCreatedAt === '') {
      this.filter.toCreatedAt = null
    }
    if (this.filter.network === '') {
      this.filter.network = null
    }
    this.$emit('filter', { ...this.filter })
  }

  onlyNumber(event: KeyboardEvent, type: string): void {
    let keyCode = event.keyCode ? event.keyCode : event.which
    //if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) {
    // 46 is dot
    if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) {
      event.preventDefault()
    }
    if (keyCode === 46 && includes(this.filter[type], '.')) {
      event.preventDefault()
    }
  }

  numberFormat(event: FocusEvent): void {
    const _event: any = event
    let fnumber = _event.target.value
    if (fnumber.length > 0) {
      fnumber = fnumber.replaceAll(',', '')
      fnumber = this.$options.filters?.numberWithCommas(fnumber)
      _event.target.value = fnumber
    }
  }
}
